/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*=========以上是CSS reset=========*/

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

.wrap {
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

nav {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background: #edede9;
}

.title {
  display: flex;
  align-items: center;
}

.title h1 {
  display: inline-block;
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 1.2px;
}

.title img {
  width: 5%;
  margin-left: 10px;
}

.aside {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
  background: #f0f8ff;
  display: none;
  width: 150px;
  height: 100%;
}

.aside-block {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
  background: #f0f8ff;
  display: block;
  width: 150px;
  height: 100%;
}

.aside-block a {
  text-decoration: none;
  padding: 10px;
  margin-right: 10px;
  color: #000;
  display: block;
}


.aside a {
  text-decoration: none;
  padding: 10px;
  margin-right: 10px;
  color: #000;
  display: block;
}

.close-menu {
  float: right;
}

.aside li {
  float: left;
}

.menu {
  display: flex;
  align-items: center;
}

.menu li {
  width: 90px;
  margin: 10px 0px;
}

.menu li a {
  padding: 10px;
  text-decoration: none;
  color: #000;
}

.menu li a:hover {
  border-bottom: 1px solid #000;
}

.show-menu {
  display: none;
}

.welcome {
  background: #e0e0e0;
}

.welcome h2 {
  text-align: center;
  font-weight: bolder;
  font-size: 30px;
  padding: 20px 0px;
}

.welcome p {
  text-align: center;
  padding: 10px;
  line-height: 20px;
}

.content {
  background: #f5f3f4;
  max-width: 1200px;
  margin: 0 auto;
}

.content2 {
  display: none;
}

.content-block {
  display: block;
}

.content h2 {
  text-align: center;
  font-weight: bolder;
  font-size: 30px;
  padding-top: 30px;
}

.content-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.content-list li {
  width: 40%;
  margin: 2%;
}

.content-list p {
  text-align: center;
  padding: 13px;
}

.content-list img {
  transition: 0.5s;
}

.content-list img:hover {
  transform: scale(1.05);
}

button {
  margin: 10px auto;
  display: block;
  padding: 5px;
  background: #f5f3f4;
}

@media (max-width: 800px) {
  .show-menu {
    display: block;
    width: 25%;
    margin-right: 5px;
  }

  .menu {
    display: none;
  }
}

@media (max-width: 500px) {
  .content-list li {
    width: 95%;
    margin: 2%;
  }
}